@import url(http://fonts.cdnfonts.com/css/gotham-rounded?styles=36543,36545);
:root {
    --speed:500ms;
}

ul {
    list-style:none;
    margin:0;
    padding:0;
}


*{
    margin:0;
    caret-color: transparent;
}

body {
    margin: 0;
    font-family: 'Gotham Rounded', sans-serif;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 300;
}

code{
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

.header{
    display:flex;
    justify-content: space-between;
    border-bottom: 1px solid #f9f9f9;
    align-items:center;

}

.header__logo {
    height:25px;
    object-fit:contain;
}

.header__icon {
    padding: 1px;
    margin: 20px;
}
.card {
    position:relative;
    width:50vh;
    padding:20px;
    max-width: 85vw;
    height:50vh;
    border-radius: 17px;
    background-size: cover;
    background-position: center;
    box-shadow:0px 18px 53px 0px rgba(0,0,0,0.3);
}

.tinderCards_cardContainer {
    display:flex;
    justify-content: center;
    margin-top:7vh;

}

.swipe {
    position:absolute;
}


.card__details {
    position:absolute;
    bottom:0px;
    left:0;
    border-bottom-left-radius: 17px;
    border-bottom-right-radius: 17px;
    background-color: white;
    height: 50px;
    width:50vh;
    padding:10px 20px 5px;
    max-width: 85vw;
    
    

}
.card__details > h3 {
    color: black;
}

.card__details > p {
    color: black;
    font-weight:300;
}
.swipeButtons {
    position:fixed;
    bottom:10vh;
    width:100%;
    display:flex;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root {
    background-color: white;
    padding:1rem !important;
    box-shadow:0px 10px 53px 0px rgba(0,0,0,0.3) !important;
}

@media screen and (max-width: 768px) {
    .swipeButtons .MuiIconButton-root {
        padding:0.7rem !important;
    }
}

.swipeButtons__repeat {
    color: #f5b748 !important;
}

.swipeButtons__left {
    color: #ec536f !important;
}

.swipeButtons__star {
    color: #62b4f9 !important;
}

.swipeButtons__right {
    color: #76e2b3 !important;
}

.swipeButtons__lightning {
    color: #915dd1 !important;
}

.chat{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:20px;
    height:80px;
    border-bottom: 1px solid #fafafa;
}

a{
    text-decoration: none;
    color:inherit;
}
.chat__image{
    margin-right:30px;
}

.chat__details{
   flex:1 1;
}

.chat__details > p {
    color: #9d9b9b;
}

.chat__details > h2 {
    color: #505050;
}

.chat__timestamp {
    color: #d3d3d3;
}
.chatScreen{
    font-family: 'Helvetica', 'Arial', sans-serif;
    
}
.chatScreen__text{
    margin-left:10px;
    background-color: #d8d8d8;
    border-radius: 20px;
    padding:15px;
    

    
}

.chatScreen__message{
    display:flex;
    align-items: center;
    padding:30px;
    
}

.chatScreen__timestamp {
    text-align: center;
    padding:20px;
    color:gray;
}

.chatScreen__textUser {
    margin-left: auto;
    background-color: #29b3cd;
    padding:15px;
    border-radius: 20px;
    color:white;
}

.chatScreen__input {
    display:flex;
    
    padding:5px;
    position:fixed;
    bottom:0;
    width:100%;
    border-top:1px solid #fafafa;
}

.chatScreen__inputField {
    flex:1 1;
    padding:10px;
    border:none;
}
.chatScreen__inputButton {
    border:none;
    margin-right:20px;
    background-color: white;
    font-weight: bolder;
    color:#fe3d71;
}
.profile{
    flex-direction: column;
    align-items: center;
    display:flex;

    padding:20px;

}

@media screen and (max-width: 768px) {
    
}

label {
    font-size: 14px;
    text-transform: uppercase;
    color:#a7a7a7;
}
.profile__image {
    box-shadow:0px 18px 53px 0px rgba(0,0,0,0.2);
}

.profile__info {
    padding:30px 0 10px;
    font-size: 20px;
    text-align: center;
    color: #616161;
}


.profile__buttonsBottom {
    display: flex;
    width:100%;
    justify-content: center;
    margin-top: -60px;

}

.profile__buttonsTop{
    display: flex;
    justify-content: space-evenly;
}

.profile__button {
    display:flex;
    flex-direction: column;
    text-align:center;
}

.profile__camera {
    color:white;
 
}


.profile__button .MuiIconButton-root {

    padding:1rem !important;
    margin: 1rem 3rem 0.8rem;
    box-shadow:0px 10px 53px 0px rgba(0,0,0,0.2) !important;
}

.profile__upgrades{
    padding-top: 35px;
    display:flex;
    align-items: center;
    justify-content: center;
}

.profile__aspects > p{
    padding:15px;
    font-size:15px;
    text-align: center;
}

.profile__aspects > Button{
    padding: 15px 30px 15px;
    border-radius: 30px;
    color: #fe3d71;
    background-color: white;
    box-shadow:0px 10px 53px 0px rgba(0,0,0,0.3) !important;
    letter-spacing: 1px;
    margin-bottom: 1rem;
    margin-top: 1rem;
    font-weight: 400;
    font-family: inherit;

}

.profile__bolt {
    color:purple !important;
}

.profile__aspects {
    padding:20px;
    display:flex;
    flex-direction: column;
    align-items: center;
   
}

.profile__svg{
position: absolute;
bottom:40px;
z-index: -100;

}

.profile__page{
    position:relative;
}

.box {
    width: 100%;
    height: 20%;
    position: absolute;
bottom:0;
z-index: -100;
background-color: #efefef;
}
